#root {
  height: 100%;
}

#body {
    margin: 0px;
    font-family: "Montserrat", sans-serif;
    font-weight: 500; 
    display: flex;
    flex-direction: column;
    height: 100vh;
}

#mapContainer {
  width: 100vw;
  flex-shrink: 1;
  overflow: hidden;
}

#map {
    width: 100%;
    height: 100%;
}
  
  nav {
    background-color:#0f4d90;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-basis: 100px;
    -webkit-box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.44); 
    box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.44);  
    z-index: 3;
    position: relative;
    flex-shrink: 0;
    flex-grow: 1;
  }
  
