  #sidebar {
    width: 200px;
    background-color: #0f4d90;
    color: white;
    font-size: 14px;
    position: relative;
    z-index: 3;
    -webkit-box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.44); 
    box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.44);
  }

  #map #sidebar {
    margin: 50px;
    height: calc(100% - 140px);
  }
  
  #results-info {
    position: absolute;
    bottom: 0px;
    right: 0px;
    z-index: 5;
    background-color: #0f4d90;
    color: white;
    padding: 5px 10px;
    margin: 20px 10px;
    -webkit-box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.44); 
    box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.44);
  }
  
  .bold {
    font-weight: 600;
  }

  #siteSearch {
    background-color: #0f4d90;
    color: white;
    font-size: 14px;
    flex: 0 0 210px;
    z-index: 3;
    -webkit-box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.44); 
    box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.44);  
    padding: 5px 5px;
    overflow-y: scroll;
    position: absolute;
    right: 50px;
    top: 120px;
    height: 200px;
    /* border-radius: 10px; */
  }
  
  #clearSearch {
    border-width: 0px;
    padding: 5px;
    margin: 0 auto;
    width: 120px;
    font-weight: 400;
    color:white;
    background-color: #2176d2;
    display: block;
    cursor: pointer;
  }
  
  #clearSearch:hover {
    opacity: 0.8;
  }

  #closeSearchModal {
    border-width: 0px;
    /* padding: 5px; */
    width: 25px;
    font-weight: 400;
    color:white;
    background-color: #2176d2;
    display: block;
    cursor: pointer;
  }